* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

li {
  list-style: none;
  display: flex;
  align-items: center;
}

.country-list__item-flag {
  margin-right: 10px;
}

.country__name {
  font-weight: bold;
  font-size: 35px;
  display: inline;
}

.country__flag {
  display:inline;
  margin-right: 15px;
}